import React from 'react'
import './Circles.css'

const Circles = (props) => {
    return (
        <div class="Circles">
            {props.circleNo.map((circle, idx) => 
            <img
            height='80'
            src={props.circleIdx === idx ? "data:image/png;base64," : "data:image/jpeg;base64,"}
            alt="circle.img"
            onClick={() => props.handleCircleClick(idx)}
           />
            )}
        </div>
    )
    
}

export default Circles;